/*

Initialization/Defaults
--------------------------------------------*/
/* --- Block Elements --- */
html {
  width: 100%;
  height: 100%;
  background: #fff;
  font-size: 62.5%;
  color: #000; }

/* 62.5% = 10px = 1.0rem */
a img {
  border: none; }

/* --- Block Text --- */
div {
  margin: 0; }

/* Localize if necessary */
/* --- Text Elements --- */
a {
  color: #000;
  text-decoration: underline; }

a:hover,
a:focus,
a:active {
  text-decoration: none; }

strong {
  font-weight: bold; }

em {
  font-style: italic; }

/* --- Form/Inputs --- */
form {
  margin: 0;
  padding: 0; }

fieldset {
  margin: 0;
  padding: 0;
  border: 0; }

legend {
  color: #000; }

label {
  display: block;
  font-size: 14px;
  font-size: 1.4rem;
  color: #000;
  cursor: pointer; }

input[type="text"] {
  margin-bottom: 0;
  padding: 3px 5px;
  border: 2px solid #ccc;
  font-size: 15px;
  font-size: 1.5rem;
  color: #000; }

input[type="radio"],
input[type="checkbox"] {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  vertical-align: middle; }

input[type="text"][disabled] {
  background: #fff; }

::-webkit-input-placeholder {
  color: #000; }

::-moz-placeholder {
  color: #000; }

/* firefox 19+ */
:-ms-input-placeholder {
  color: #000; }

/* ie */
input:-moz-placeholder {
  color: #000; }

/* 
     Utility ID's
     ----------------------- */
.clear {
  display: block;
  clear: both;
  overflow: hidden;
  width: 1px;
  height: 1px;
  margin: 0 -1px -1px 0;
  border: 0;
  font-size: 0;
  line-height: 0; }

.hide {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 0 !important;
  height: 0; }

